/* Верхнее меню */

/* Категории  */
.categories div {
    
    display: inline-block;
    background: #f5ae00;
border-radius: 50px;
padding: 10px 40px;
margin-bottom: 25px;
margin-right: 15px;
cursor: pointer;
border: 1px solid transparent;
transition: all 500ms ease;
}

.categories div:hover {
    border-color: silver;
    transform: scale(1.1);

}
/* Категории */

/* cart */
main {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item {
    width: 30%;
    margin-bottom: 50px;
    background: #f5f5f5;
    overflow: hidden;
    position: relative;
    padding-bottom: 20px;
    border-radius: 10%;
}

main .item img, .full-item img {
    width: 100%;
    border-radius: 10px 161px 0 0;
    transition: transform 500ms ease;
    cursor: pointer;
}

main .item img:hover, .full-item img:hover {
    transform: scale(1.05);
}

main h2,.full-item h2,
 main p, .full-item p,
  main b, .full-item b  {
    margin: 10px 20px;
    color: #333;
}

main b, .full-item b {
    color: #5fa36a;
}


main .add-to-cart,
.full-item .add-to-cart{
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #52ca64;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-weight: 600;
    transition: transform 500ms ease;
}

main .add-to-cart:hover,
 .full-item .add-to-cart:hover{
    transform: scale(1.5) translateY(-5px);
}

.full-item {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    z-index: 9999;
    overflow: auto;
}

.full-item > div {
    width: 600px;
    position: relative;
    margin: 10% auto;
    padding: 40px 30px;
    background: #fff;
    border-radius: 9px;
}

/* cart */

/* Вход в модальном окне*/

.form {
    width: 300px;
    padding: 32px;
    border-radius:10px;
    box-shadow: 0 4px 16px #ccc;
    font-family: sans-serif;
    letter-spacing: 1px;
}

.form__input,
.form__button {
font-family: sans-serif;
letter-spacing: 1px;
font-size: 16px;
}

.form__input,
.form__button2 {
font-family: sans-serif;
letter-spacing: 1px;
font-size: 16px;
}

.form__title {
    text-align: center;
    margin: 0 0 32px 0;
    font-weight: normal;
}

.form__group {
    position: relative;
    margin-bottom: 32px;
}

.form__label {
    position: absolute;
    top: 0;
    z-index: -1;
    color: #9e9e9e;
    transition: 0.3s;
    left: 0;
}

.form__input {
    width: 100%;
    padding: 0 0 10px 0;
    border: none;
    border-bottom: 1px solid #e0e0e0;
    background-color: transparent;
    outline: none;
    transition: 0.3s;
}


.form__input:focus {
    border-bottom: 1px solid #1a73a8;
    }
    
    .form__button {
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        color: #fff;
        background-color: #0071f0;
        outline: none;
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        left: -10px;
    }
    
    .form__button:focus,
    .form__button:hover {
        background-color: rgba(0, 113, 240, 0.7);
    }
    
    .form__button2 {
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        color: #fff;
        background-color: #0071f0;
        outline: none;
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        left: 15px;
    }
    
    .form__button2:focus,
    .form__button2:hover {
        background-color: rgba(0, 113, 240, 0.7);
    }



    .form__input:focus ~ .form__label,
    .form__input:not(:placeholder-shown) ~ .form__label {
        top: -18px;
        font-size: 12px;
        color: #e0e0e0;
    }
    
