* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  .open-btn {
    background: #fe4164;
 
    padding: 5px;
 
    border-radius: 10px 20px;
  }
  
  .modal__button {
    position: relative;
    text-align: center;
    justify-content: center;
    left: 110px;
    top: 15px;
    width: 80px;
    height: 50px;
    color: white;
    background-color: rgb(76, 22, 222);
  
    border: 6px solid rgb(76, 22, 222);
    border-radius: 10px;
  }
  
  .text__color {
    color: gray;
  }